import { baseImgURL } from 'assets/Constant';
import React from 'react'
import cssVar from "views/mobileView/styles";

const GoalIcon = ({type,height,width}) => {

    var goals = [
        {
            icon: baseImgURL+"retire_goal.webp",
            goalType: 1,
            color: cssVar.getPropertyValue('--secondaryGreen')
        }, {
            icon: baseImgURL+"home_goal.svg",
            goalType: 5,
            color: cssVar.getPropertyValue('--secondaryGreen')
        }, {
            icon: baseImgURL+"education_goal.webp",
            goalType: 3,
            color: cssVar.getPropertyValue('--red')
        }, {
            icon: baseImgURL+"marriage_goal.webp",
            goalType: 4,
            color: cssVar.getPropertyValue('--red')
        }, {
            icon: baseImgURL+"buyBike_goal.webp",
            goalType: 2,
            color: '#9F87E8'
        }, {
            icon: baseImgURL+"buyCar_goal.webp",
            goalType: 6,
            color: '#9F87E8'
        }, {
            icon: baseImgURL+"vacation_goal.webp",
            goalType: 7,
            color: cssVar.getPropertyValue('--gold')
        }, {
            icon: baseImgURL+"custom_goal.webp",
            goalType: 8,
            color: cssVar.getPropertyValue('--gold')
        }, {
            icon: baseImgURL+"custom_goal.webp",
            goalType: undefined,
            color: cssVar.getPropertyValue('--gold')
        }, {
            icon: baseImgURL+"custom_goal.webp",
            goalType: 0,
            color: cssVar.getPropertyValue('--gold')
        }
    ]

    const goal = goals.find(g=>g.goalType === type)
  return(
    <div style={{ display:'flex', border: "1px solid " + goal?.color,padding:`calc(${height}*10/100) 0`, borderRadius: '4px', height, width, backgroundColor: goal?.color+'22'}}>
        <img src={goal?.icon} style={{height: `calc(${height}*80/100)`,margin:'auto'}}/>
    </div>
  )
}

export default React.memo(GoalIcon)